import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { shortenAddress } from '@usedapp/core';
import { motion } from 'framer-motion';

const NFTSeries = [
  {
    value: '0x685B1ded8013785d6623CC18D214320b6Bb64759',
    label: `Pincubator WAAB Series (${shortenAddress('0x685B1ded8013785d6623CC18D214320b6Bb64759')})`
  },
  {
    value: '0x685B1ded8013785d6623CC18D214320b6Bb64759',
    label: `Pincubator WAAB Series (${shortenAddress('0x685B1ded8013785d6623CC18D214320b6Bb64759')})`
  },
  {
    value: '0x685B1ded8013785d6623CC18D214320b6Bb64759',
    label: `Pincubator WAAB Series (${shortenAddress('0x685B1ded8013785d6623CC18D214320b6Bb64759')})`
  }
];

const CheckNFT = (props) => {
  const {
    register,
    handleSubmit,
    formState: { errors }
  } = useForm();
  const [result, setResult] = useState('');

  const handleCheckNFTSubmit = (data) => {
    setResult(JSON.stringify(data));
    alert(`NFT CHECK: ${JSON.stringify(data)}`);
  };

  return (
    <motion.div
      initial={{ opacity: 0, y: '-20vh' }}
      animate={{ opacity: 1, y: '0' }}
      exit={{ opacity: 0 }}
      transition={{ duration: 1, type: 'spring' }}
      className="container pt-5 d-flex flex-column justify-content-center align-items-center"
      style={{ height: '100%', minHeight: '90vh' }}
    >
      <h2>Check Pincubator NFT</h2>
      <form className="checkNFT-form" onSubmit={handleSubmit((data) => handleCheckNFTSubmit(data))}>
        <select {...register('nftseries', { required: true })} className="form-select purple-input">
          {NFTSeries.map((data, i) => {
            return (
              <option key={i} value={data.value}>
                {data.label}
              </option>
            );
          })}
        </select>

        <input
          {...register('tokenid', { required: true })}
          className="form-control mt-2 purple-input"
          type="number"
          placeholder="Your token ID"
        />
        {errors.tokenid && <span className="invalid-danger">This field is required</span>}

        <button className="button-outline mt-5 w-100 purple-fill" type="submit" value="submit">
          CHECK
        </button>
      </form>

      {/* <p style={{ lineBreak: 'anywhere' }}>{result}</p> */}

      <button className="button-outline mt-5" onClick={props.toHome}>
        Go Back
      </button>
    </motion.div>
  );
};

export default CheckNFT;
