import { motion } from 'framer-motion';

const dropIn = {
  hidden: {
    y: '-100vh',
    opacity: 0,
    transition: {
      duration: 0.3,
      type: 'spring',
      damping: 25,
      stiffnesss: 500
    }
  },
  visible: {
    y: '0',
    opacity: 1
  },
  exit: {
    y: '100vh',
    opacity: 0
  }
};

const Backdrop = (props) => {
  return (
    <motion.div
      className="dialog-backdrop"
      onClick={props.onClick}
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
    >
      {props.children}
    </motion.div>
  );
};

export default Backdrop;
