import { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { useEthers, shortenAddress } from '@usedapp/core';
import { wallets, connectorsByName, connectorsNames } from '../../connectors';
// components
import CustomDialog from '../Dialog/CustomDialog';

const Header = (props) => {
  const [connectedState, setConnectedState] = useState(false);

  const [walletModalOpen, setWalletModalOpen] = useState(false);

  const { account, activate } = useEthers();

  const { error } = useEthers();

  const [activateError, setActivateError] = useState('');

  useEffect(() => {
    if (error) {
      setActivateError(error.message);
    }
  }, [error]);

  const handleConnectWallet = (value) => {
    activate(connectorsByName[value]);
    setActivateError('');
    setWalletModalOpen(false);
  };

  const handleOpenWalletModal = async () => {
    setActivateError('');
    setWalletModalOpen(true);
  };

  const handleCloseWalletModal = () => {
    setWalletModalOpen(false);
  };

  return (
    <>
      <div className="header-wrapper">
        {!account ? (
          <button
            onClick={handleOpenWalletModal}
            className="connect-wallet-btn unconnected color-purple pe-all"
            id="wallet-unconnected"
          >
            <img src="/static/img/wallet.svg" alt="" className="wallet-icon" />
            {activateError ? <span>{activateError}</span> : <span>Connect Wallet</span>}
          </button>
        ) : (
          <button
            onClick={handleOpenWalletModal}
            className="connect-wallet-btn connected color-green pe-all"
            id="wallet-unconnected"
          >
            Connected ({shortenAddress(account)})
          </button>
        )}
      </div>

      <AnimatePresence initial={false} exitBeforeEnter={true} onExitComplete={() => null}>
        {walletModalOpen && (
          <CustomDialog modalOpen={walletModalOpen} handleClose={handleCloseWalletModal}>
            <h2 className="text-center mt-3">Connect Wallet</h2>

            <div
              className="d-flex flex-wrap flex-column align-items-center mt-3 mb-3"
              style={{ width: '90%', marginInline: 'auto' }}
            >
              <h6>Connect with</h6>

              <button
                className="choose-wallet-btn m-2"
                value="metamask"
                onClick={() => handleConnectWallet('metamask')}
              >
                <img src="/static/img/metamask.jpg" alt="" className="wallet-logo" />
                Metamask
              </button>

              <button
                className="choose-wallet-btn m-2"
                value="walletConnect"
                onClick={() => handleConnectWallet('walletconnect')}
              >
                <img src="/static/img/walletconnect.jpg" alt="" className="wallet-logo" />
                <span>WalletConnect</span>
              </button>


              {/*<button className="choose-wallet-btn m-2" value="Binance" onClick={handleConnectWallet}>
                                <img src="/static/img/binance.jpg" alt="" className="wallet-logo" />
                                Binance
                            </button> 
                            */}
            </div>
          </CustomDialog>
        )}
      </AnimatePresence>
    </>
  );
};

export default Header;
