import { motion } from 'framer-motion';
import { Helmet } from 'react-helmet-async';
import Backdrop from './Backdrop';

const dropIn = {
  hidden: {
    y: '-50vh',
    opacity: 0
  },
  visible: {
    y: '0',
    opacity: 1,
    transition: {
      duration: 1,
      type: 'spring',
      damping: 15,
      stiffnesss: 300
    }
  },
  exit: {
    y: '50vh',
    opacity: 0
  }
};

const CustomDialog = (props) => {
  return (
    <>
      <Helmet>
        <style>{'body { overflow-y: hidden; }'}</style>
      </Helmet>

      <Backdrop onClick={props.handleClose}>
        <motion.div
          variants={dropIn}
          initial="hidden"
          animate="visible"
          exit="exit"
          onClick={(e) => {
            e.stopPropagation();
          }}
          className="dialog-modal"
        >
          {props.children}
        </motion.div>
      </Backdrop>
    </>
  );
};

export default CustomDialog;
