import { useEffect, useState } from 'react';
import { useContractFunction, useEthers} from '@usedapp/core';
import { SHAZU_SEED_CONTRACT} from '../../config';


export default function ApproveButton(props) {
    const { account } = useEthers();

    const [claiming, setClaiming] = useState(false)

    const { state, send } = useContractFunction(SHAZU_SEED_CONTRACT, 'claim', { transactionName: 'Claim Tokens' });

    const handleClaim = () => {
        setClaiming(true)
        send()
    }

    const handleAfterClaim = () => {
        props.setTokenBalance(props.tokenBalance)
        props.handleClaim()
    }

    useEffect(() => {
        if (state.status === 'Exception' || state.status === 'Fail') {
            setClaiming(false)
        }
    
        if (state.status === 'None') {
            setClaiming(false)
        }
    
        if (state.status === 'Success') {
            setClaiming(false)
            props.setTx(state.transaction.hash)
            handleAfterClaim()
        }
    }, [state]);

    return (
        <>
            <button className="button-outline-green mt-4 w-100" onClick={handleClaim} style={{ padding: '0.3rem' }} disabled={claiming}>
                {!claiming ? (
                'Claim'
                ): (
                'Claiming...'
                )}
            </button>
        </>
    )
}